import "./styles.css";
let colorChange = document.getElementById("color-change");
let shapeChange = document.getElementById("shape-change");
let shape = document.querySelectorAll(".c1");
let circle = document.getElementsByClassName("circle");
var count = 0;

colorChange.addEventListener("click", function () {
  const randomColor = Math.floor(Math.random() * 16777215).toString(16);
  circle[0].style.backgroundColor = "#" + randomColor;
});

shapeChange.addEventListener("click", function () {
  shape[count].style.display = "none";
  count++;
  if (count === 4) {
    count = 0;
  }
  shape[count].style.display = "inline";
});
